// schema
import Home from './assets/schema/home.json'
import NewsList from './assets/schema/news-list.json'
import NewsSingle from './assets/schema/news-single.json'
import Categories from './assets/schema/product-category.json'
import Product from './assets/schema/product-single.json'
import Inventory from './assets/schema/inventory.json'
import Inquiry from './assets/schema/inquiry.json'
import Dealer from './assets/schema/dealer.json'
import Machine from './assets/schema/machine.json'
import ErrorShooting from './assets/schema/errorshooting.json'

import Vue from 'vue'

export default {
  getPost (id, cb) {
    // Mount some real ajax calls
    Vue.http.get('https://jsonplaceholder.typicode.com/posts/' + id).then((response) => {
    // success callback
      if (response.data) {
        cb(null, response.data)
      }
    }, (response) => {
  // error callback
      cb(new Error(response.data))
    })
  },
  getHome (cb) {
    // // Mount some real ajax calls
    // Vue.http.get('/backend/api/getHome').then((response) => {
    // // success callback
    //   if (response.data) {
    //     cb(null, response.data)
    //     console.log(response)
    //   }
    // }, (response) => {
    // // error callback
    //   cb(new Error(response.data))
    //   console.log(response)
    // })
    setTimeout(() => {
      if (Home) {
        cb(null, Home)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getNewsHome (page, cb) {
    setTimeout(() => {
      if (NewsList) {
        cb(null, NewsList)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getNewsSingle (id, cb) {
    setTimeout(() => {
      if (NewsSingle) {
        cb(null, NewsSingle)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getCategories (cb) {
    setTimeout(() => {
      if (Categories) {
        cb(null, Categories)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getProduct (id, cb) {
    setTimeout(() => {
      if (Product) {
        cb(null, Product)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getProducts (id, cb) {
    setTimeout(() => {
      if (Inquiry) {
        cb(null, Inquiry)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getInventory (cb) {
    setTimeout(() => {
      if (Inventory) {
        cb(null, Inventory)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getDealer (cb) {
    setTimeout(() => {
      if (Dealer) {
        cb(null, Dealer)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getMachine (cb) {
    setTimeout(() => {
      if (Machine) {
        cb(null, Machine)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  },
  getSolution (id, code, cb) {
    setTimeout(() => {
      if (ErrorShooting) {
        cb(null, ErrorShooting)
      } else {
        cb(new Error('Data not found.'))
      }
    }, 500)
  }
}



